#page-room {
  padding-bottom: 30px;

  header {
    padding: 24px;
    border-bottom: 1px solid #e2e2e2;

    .content {
      max-width: 1120px;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-between;

      > div {
        display: flex;
        align-items: center;
        gap: 8px;
      }
    }

    > img {
      max-height: 45px;
    }

    .buttons-header {
      display: flex;
      flex-direction: row;
      gap: 16px;
    }
  }

  main {
    max-width: 800px;
    width: 80%;
    margin: 0 auto;

    .room-title {
      margin: 32px 0 24px;
      display: flex;
      align-items: center;
      h1 {
        font-family: Poppins, sans-serif;
        font-size: 24px;
        color: #29292e;
      }
  
      span {
        margin-left: 16px;
        background-color: #e559f9;
        border-radius: 9999px;
        padding: 8px 16px;
        color:  #fff;
        font-weight: 500;
        font-size: 14px;
        white-space: nowrap;
      }
    }

    form {
      textarea {
        width: 100%;
        border: 0;
        padding: 16px;
        border-radius: 8px;
        background-color: #fefefe;
        box-shadow: 0 2px 12px rgba(0, 0, 0, 0.84);
        resize: vertical;
        min-height: 138px;
      }
    }

    .form-footer {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 16px;

      .user-info {
        display: flex;
        align-items: center;

        img {
          width: 45px;
          height: 45px;
          border-radius: 50%;
        }

        span {
          margin-left: 8px;
          color: #29292e;
          font-weight: 500;
          font-size: 14px;
        }
      }

      > span {
        font-size: 14px;
        color: #737380;
        font-weight: 500;

        button {
          background-color: transparent;
          border: 0;
          color: #835afd;
          text-decoration: underline;
          font-size: 14px;
          cursor: pointer;
        }
      }
    }

    .question-list {
      margin-top: 48px;

      .question-container {
        position: relative;

        .question {
          padding-top: 28px;
        }
      }
    }
  }

}

@media screen and (max-width: 650px) {
  #page-room {
  
    header {
      padding: 24px;
  
      .content {
        flex-wrap: wrap;
        flex-direction: column;

        > img {
          height: 60px;
          margin-bottom: 8px;
        }
      }
    }
  
    main {
      width: 90%;
  
      .room-title {
        h1 {
          font-size: 20px;
        }
    
        span {
          padding: 4px 12px;
          font-size: 12px;
        }
      }
  
      form {
        textarea {
          width: 100%;
          padding: 12px;
          resize: vertical;
          font-size: 14px;
        }
      }
  
      .form-footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 16px;
  
        .user-info {
          img {
            height: 34px;
            width: 34px;
          }
  
          span {
            font-size: 12px;
          }
        }
  
        > span {
          font-size: 12px;
          font-weight: 300;
  
          button {
            font-size: 12px;
          }
        }

        .button {
          font-size: 14px;
          padding: 0 24px;
          margin-left: 12px;
        }
      }
  
      .question-list {
        // .question-container {
        //   position: relative;
  
        //   .question {
        //     padding-top: 28px;
        //   }
        // }
      }

      .question-content {
        p {
          font-size: 14px;
        }
        
        footer {
          .user-info {
            img {
              height: 34px;
              width: 34px;
            }
    
            span {
              font-size: 12px;
            }
          }

          .interactive-buttons {
            span {
              font-size: 14px;
            }

            svg path{
              height: 38px;
              width: 38px;
            }
          }

        }
      }
    }
  
  }
}
